
.typewriterBlock{
    
    line-height: 1.6rem;
    position: absolute;
    z-index: 300;
}

@media (max-width: 3500px) {
    .typewriterBlock{
        letter-spacing: 4px;
        left: 20%;
        top: 20%;
        font-size: 1.6rem;
        line-height: 2.4rem;
    }
}
