.globusBlock {
    position: relative;
    width: 100%;
    margin: 0px;
    padding: 0px;
}
.quote{
    display: flex;
    align-items: center;
    justify-content: center;
}
.info{
    margin-top: 30px;
    padding: 5px;
    p{
        font-size: 1.2rem;
        line-height: 1.5rem;
        font-weight: 400;
        text-indent: 1rem;
    }
}
@media (max-width: 3500px) {
    .globusBlock {
        height: 1200px;
    }
}
@media (max-width: 1801px) {
    .globusBlock {
        height: 1150px;
    }
}
@media (max-width: 1401px) {
    .globusBlock {
        height: 1050px;
    }
}
@media (max-width: 1200px) {
    .globusBlock {
        height: 950px;
    }
}
@media (max-width: 992px) {
    .globusBlock {
        height: 800px;
    }
}
@media (max-width: 768px) {

}
@media (max-width: 576px) {
	.globusBlock {
        height: 500px;
    }
}